import React, { useEffect, useState } from 'react';
import { Route, createBrowserRouter, createRoutesFromElements, Link, Navigate, RouterProvider } from 'react-router-dom';
import Cookies from 'js-cookie'
import 'bootstrap/dist/css/bootstrap.css'
import './assets/App.css';
import './assets/fonts/fonts.css';

import AuthContext from './context/AuthContext';
import { Login, Playground, Screen2, Screen3, Screen4, Screen5, Screen7, Screen8 } from "./pages";

const App = () => {
  const [auth, setAuth] = useState(false);
  const [accountCreated, setAccountCreated] = useState(false);

  const readCookie = () => {
    const user = Cookies.get("token");
    if (user) {
      setAuth(true)
    }
  }

  useEffect(() => {
    readCookie();
  }, [])

  const router = createBrowserRouter(
    createRoutesFromElements(
      // <Route path='/' element={<Root/>}>
      //   <Route path='/login' element={<Login />} />
      //   {/* <Route path='/register' element={<Register />} /> */}

      //   <Route path='/dashboard' element={<Playground/>}/>
      //   {/* <Route path='/dashboard/welcome' element={<div>Welcome user</div>} /> */}
      // </Route>
      <>
        <Route path='/' element={<Login />} />
        <Route path='/play' element={<Playground />} />
        <Route path='/screen2' element={<Screen2 />} />
        <Route path='/screen3' element={<Screen3 />} />
        <Route path='/screen4' element={<Screen4 />} />
        <Route path='/screen5' element={<Screen5 />} />
        <Route path='/screen7' element={<Screen7 />} />
        <Route path='/screen8' element={<Screen8 />} />
      </>
    )
  )

  return (
    <AuthContext.Provider value={{ auth, setAuth, setAccountCreated, accountCreated }}>
      <RouterProvider router={router} />
    </AuthContext.Provider>
  )
};

export default App;