import React, { useState, useEffect } from 'react';
import logo from '../../assets/images/logo.png';
import bird from '../../assets/images/bird.png'; 
import bear from '../../assets/images/bear.png';
import b from '../../assets/images/b.png'; 
import star from '../../assets/images/star.png';
import horse from '../../assets/images/horse.png';
import eagle from '../../assets/images/eagle.png';
import Leopard from '../../assets/images/Leopard.png';
import helmet from '../../assets/images/helmet.png';
import bigbear from '../../assets/images/bigbear.png';
import bigbird from '../../assets/images/bigbird.png';
import moneywave from '../../assets/images/moneywave.svg';
import burgerIcon from '../../assets/images/burger-icon.svg';
import closeIcon from '../../assets/images/close-icon.svg';
import profile from '../../assets/images/profile.png';
import user from '../../assets/images/user.svg';
import soccerball from '../../assets/images/soccerball.svg';
import srcard from '../../assets/images/srcard.svg';
import clock from '../../assets/images/clock.svg';
import setting from '../../assets/images/setting.svg';
import Helpinfo from '../../assets/images/Helpinfo.svg';
import logout from '../../assets/images/logout.svg';
import srbook from '../../assets/images/srbook.svg';
import './index.css';
import { Col, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';

const Screen2 = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [selectedCenterDetail, setSelectedCenterDetail] = useState(null);
  const [selectedTeam, setSelectedTeam] = useState(null);

  const handleMenuToggle = () => {
    setMenuOpen(!menuOpen);
  };

  useEffect(() => {
    if (menuOpen) {
      document.body.style.overflowX = 'hidden';
      document.body.style.overflowY = 'hidden';
      document.documentElement.style.overflowX = 'hidden';
      document.documentElement.style.overflowY = 'hidden';
    } else {
      document.body.style.overflow = '';
      document.documentElement.style.overflow = '';
    }
  }, [menuOpen]);

  const handleCenterDetailClick = (index) => {
    setSelectedCenterDetail(index);
  };

  const handleTeamClick = (index) => {
    setSelectedTeam(index);
  };

  return (
    <>
      <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
        <div className='burger-icon' onClick={handleMenuToggle}>
          <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
        </div>
        <div className='screen-2-section'>
          <div className='screen-2-content'>
            <div className='screen-top-part'>
              <div className='logo-part'>
                <img src={logo} alt="logo" className='img-fluid' />
              </div>
              <span className='screen-title'>Pick a Sport</span>
              <div className='sport-list'>
                <ul>
                  <li><Link>NFL</Link></li>
                  <li><Link>NBA</Link></li>
                  <li><Link>MLB</Link></li>
                  <li><Link>NCAA</Link></li>
                  <li><Link>NHL</Link></li>
                  <li><Link>NFL</Link></li>
                  <li><Link>NBA</Link></li>
                  <li><Link>MLB </Link></li>
                </ul>
              </div>
            </div>
            <div className='screen-2-centerpart'>
              <span className='screen-purple-glow-title'>Pick a Game</span>
              <div className='screen-2-center-block'>
                <Row>
                  {[
                    { img1: bird, img2: bear },
                    { img1: b, img2: star },
                    { img1: horse, img2: eagle },
                    { img1: Leopard, img2: helmet },
                  ].map((item, index) => (
                    <Col xs={6} key={index}>
                      <div
                        className={`screen-2-center-detail ${selectedCenterDetail === index ? 'selected' : ''}`}
                        onClick={() => handleCenterDetailClick(index)}
                      >
                        <img src={item.img1} alt="team 1" className='img-fluid' />
                        <span>vs</span>
                        <img src={item.img2} alt="team 2" className='img-fluid' />
                      </div>
                    </Col>
                  ))}
                </Row>
              </div>
            </div>
            <div className='screen-2-bottom-part'>
              <span className='screen-purple-glow-title'>Pick a Team</span>
              <div className='screen-2-bottom-block'>
                <div
                  className={`screen-2-bottom-name-block ${selectedTeam === 0 ? 'selected' : ''}`}
                  onClick={() => handleTeamClick(0)}
                >
                  <div className='screen-2-team-name'>
                    <img src={bigbear} alt="Chicago Bears" />
                  </div>
                  <span className='screen-2-team-name-title'>Chicago Bears</span>
                </div>
                <span className='screen-2-opposite-title'>vs</span>
                <div
                  className={`screen-2-bottom-name-block ${selectedTeam === 1 ? 'selected' : ''}`}
                  onClick={() => handleTeamClick(1)}
                >
                  <div className='screen-2-team-name'>
                    <img src={bigbird} alt="Arizona Cardinals" />
                  </div>
                  <span className='screen-2-team-name-title'>Arizona Cardinals</span>
                </div>
              </div>
            </div>
            <div className='screen-2-bottom-button'>
              <Link to="/screen3">
                <img src={moneywave} alt="moneywave" className='img-fluid' />
                Play For Money
              </Link>
              <Link to="/screen4">Play For Fun</Link>
            </div>
          </div>
        </div>
      </div>
 
      {/* Side Menu */}
      <div className={`side-menu ${menuOpen ? 'open' : ''}`}>
        <div className='side-menu-content'>
          <div className='profile-photo-block'>
            <div className='profile-photo'>
              <img src={profile} alt="profile" className='img-fluid' />
            </div>
            <div className='profile-name-info'>
              <h4>Henry James <span>Level 2</span></h4>
            </div>
          </div>
          <div className='money-balance'>
            <span>$546.56</span>
          </div>
          <div className='menu-list'>
            <ul>
              <li><Link><img src={user} alt="user" className='img-fluid' />My Account</Link></li>
              <li><Link><img src={soccerball} alt="soccerball" className='img-fluid' />Active/Pending Games</Link></li>
              <li><Link><img src={srcard} alt="srcard" className='img-fluid' />Payout Source</Link></li>
              <li><Link><img src={clock} alt="clock" className='img-fluid' />Game History</Link></li>
              <li><Link><img src={setting} alt="setting" className='img-fluid' />Settings</Link></li>
              <li><Link to='/screen7'><img src={Helpinfo} alt="Helpinfouser" className='img-fluid' />Help</Link></li>
              <li><Link><img src={srbook} alt="srbook" className='img-fluid sr-book' />House Rules</Link></li>
              <li><Link><img src={logout} alt="logout" className='img-fluid' />Log out</Link></li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
};

export default Screen2;





