import React, { useEffect, useState } from 'react'
import { Button, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import './index.css';
import gameimg from '../../assets/images/gameimg.png';
import biglogo from '../../assets/images/biglogo.png';
import message from '../../assets/images/message.svg';
import lock from '../../assets/images/lock.svg'; 
import facebook from '../../assets/images/facebook.svg';
import google from '../../assets/images/google.svg'; 
import apple from '../../assets/images/apple.svg';
import hide from '../../assets/images/hide.svg';

const Screen1 = () => {

    return (
        <>
            <div className='screen-1-section'>
                <div className='screen-1-content'>
                    <div className='screen-gameimgpart'>
                        <div className='screen-gameimage'>
                            <img src={gameimg} alt="gameimg" className='img-fluid' />
                        </div>
                        <div className='screen-logo-part'>
                            <div className='screen-1-logo-image'>
                                <img src={biglogo} alt="logo" className='img-fluid' />
                            </div>
                            <div className='screen-1-main-title'>
                                <h3>BallHog</h3>
                            </div>
                        </div>
                    </div>
                    <div className='screen-form'>
                        <Form>
                            <Form.Group controlId="formGroupEmail" className='form-group'>
                                <Form.Control type="email" placeholder="Email Address" />
                                <img src={message} alt="message" className='img-fluid' />
                            </Form.Group>
                            <Form.Group controlId="formGroupPassword" className='form-group'>
                                <Form.Control type="password" placeholder="Password" />
                                <img src={lock} alt="lock" className='img-fluid' />
                                <img src={hide} alt="hide" className='hide-password' />
                            </Form.Group>
                            <div className='foregt-password-block'>
                                <Link className='forget-password'>Forgot Password?</Link>
                            </div>
                            <Link to='/screen2'>
                                <Button type="submit" className='sign-in-btn'>Sign in</Button>
                            </Link>
                        </Form>
                        <span className='devider'>or continue with</span>
                        <div className='continue-signin-group'>
                            <Link to="/" target='_blank'><img src={facebook} alt="facebook" className='img-fluid' /></Link>
                            <Link to="/" target='_blank'><img src={google} alt="facebook" className='img-fluid' /></Link>
                            <Link to="/" target='_blank'><img src={apple} alt="facebook" className='img-fluid' /></Link>
                        </div>
                        <div className='sign-up-block'>
                            <p>Don’t have an account? <Link to="/">Sign up</Link></p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Screen1
