import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import profile from '../../assets/images/profile.png';
import user from '../../assets/images/user.svg';
import soccerball from '../../assets/images/soccerball.svg';
import srcard from '../../assets/images/srcard.svg';
import clock from '../../assets/images/clock.svg';
import setting from '../../assets/images/setting.svg';
import Helpinfo from '../../assets/images/Helpinfo.svg';
import logout from '../../assets/images/logout.svg';
import burgerIcon from '../../assets/images/burger-icon.svg';
import closeIcon from '../../assets/images/close-icon.svg';
import logo from '../../assets/images/logo.png';
import manchesterlogo from '../../assets/images/manchesterlogo.png';
import reallogo from '../../assets/images/reallogo.png';
import arrowright from '../../assets/images/arrowright.svg';
import srbook from '../../assets/images/srbook.svg';
import './index.css';
import { ToggleButton, ToggleButtonGroup } from 'react-bootstrap';

const Screen3 = () => {

    const [menuOpen, setMenuOpen] = useState(false);

    const handleMenuToggle = () => {
        setMenuOpen(!menuOpen);
    };

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    const [radioValue, setRadioValue] = useState('1');

    const handleChange = (val) => setRadioValue(val);

    return (
        <>
            <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
                <div className='burger-icon' onClick={handleMenuToggle}>
                    <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
                </div>
                {/* <div className='srbook'>
                    <img src={srbook} alt="book" className='img-fluid'/>
                    <span>House Rules</span>
                </div> */}
                <div className='screen-3-section'>
                    <div className='screen-3-content'>
                        <div className='screen-top-part'>
                            <div className='logo-part'>
                                <img src={logo} alt="logo" className='img-fluid' />
                            </div>
                            <span className='screen-title'>Your Game</span>
                            <div className='select-team-group'>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        <img src={manchesterlogo} alt="manchester" className='img-fluid' />
                                    </div>
                                    <span>MAN U</span>
                                </div>
                                <div className='opposite-part'>vs <span>08:00 <span>est</span></span></div>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        <img src={reallogo} alt="manchester" className='img-fluid' />
                                    </div>
                                    <span>RM</span>
                                </div>
                            </div>
                            <div className='your-team-block'>
                                <div className='your-team'>
                                    <h4>Your Team</h4>
                                </div>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        <img src={manchesterlogo} alt="manchester" className='img-fluid' />
                                    </div>
                                    <span>MAN U</span>
                                </div>
                            </div>
                        </div>
                        <div className='select-money-block'>
                            <span className='screen-purple-glow-title'>PayOuts</span>
                            <div className='user-select-money'>
                                <ToggleButtonGroup
                                    type="radio"
                                    name="options"
                                    value={radioValue}
                                    onChange={handleChange}
                                    className="mb-2"
                                >
                                    <ToggleButton
                                        id="radio-1"
                                        value="1"
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $5
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-2"
                                        value="2"
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $10
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-3"
                                        value="3"
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $25
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-4"
                                        value="4"
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $50
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-5"
                                        value="5"
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $100
                                    </ToggleButton>
                                </ToggleButtonGroup>
                            </div>
                            <div className='fee-block'>
                                <div className='fee-info'>
                                    <p>$100 <span>Entry Fee</span></p>
                                </div>
                                <div className=''>
                                    <img src={arrowright} alt="arrow" className='img-fluid' />
                                </div>
                                <div className='fee-info'>
                                    <p>$350 <span>To Win</span></p>
                                </div>
                            </div>
                            <div className='submit-btn'>
                                <Link to="/screen5">Submit</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Side Menu */}
            <div className={`side-menu ${menuOpen ? 'open' : ''}`}>
                <div className='side-menu-content'>
                    <div className='profile-photo-block'>
                        <div className='profile-photo'>
                            <img src={profile} alt="profile" className='img-fluid' />
                        </div>
                        <div className='profile-name-info'>
                            <h4>Henry James <span>Level 2</span></h4>
                        </div>
                    </div>
                    <div className='money-balance'>
                        <span>$546.56</span>
                    </div>
                    <div className='menu-list'>
                        <ul>
                            <li><Link><img src={user} alt="user" className='img-fluid' />My Account</Link></li>
                            <li><Link><img src={soccerball} alt="soccerball" className='img-fluid' />Active/Pending Games</Link></li>
                            <li><Link><img src={srcard} alt="srcard" className='img-fluid' />Payout Source</Link></li>
                            <li><Link><img src={clock} alt="clock" className='img-fluid' />Game History</Link></li>
                            <li><Link><img src={setting} alt="setting" className='img-fluid' />Settings</Link></li>
                            <li><Link to='/screen7'><img src={Helpinfo} alt="Helpinfouser" className='img-fluid' />Help</Link></li>
                            <li><Link><img src={srbook} alt="srbook" className='img-fluid sr-book' />House Rules</Link></li>
                            <li><Link><img src={logout} alt="logout" className='img-fluid' />Log out</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Screen3
